import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";
import Tooltip from "react-bootstrap/Tooltip";
import OverlayTrigger from "react-bootstrap/OverlayTrigger";
import { useState } from "react";
import Modal from "react-modal";

function Item(props) {
  const renderTooltip = (data) => (props) =>
    (
      <Tooltip id="button-tooltip" {...props}>
        {data}
      </Tooltip>
    );
  const [compareCode1, setCompareCode1] = useState("");
  const [compareCode2, setCompareCode2] = useState("");
  const [modalIsOpen, setIsOpen] = useState(false);

  const [modalOriginalCodeIsOpen, modalOriginalCodesetIsOpen] = useState(false);

  function openModal() {
    setIsOpen(true);
  }

  function closeModal() {
    setIsOpen(false);
  }

  const handleCode2vecClick = (e) => {
    setCompareCode1(props.data.code2vecNeighbor1Code);
    setCompareCode2(props.data.code2vecNeighbor2Code);
    openModal();
  };

  const handleCodebertClick = (e) => {
    setCompareCode1(props.data.CodeBERTNeighbor1Code);
    setCompareCode2(props.data.CodeBERTNeighbor2Code);
    openModal();
  };

  const handleSbertClick = (e) => {
    setCompareCode1(props.data.SBERTNeighbor1Code);
    setCompareCode2(props.data.SBERTNeighbor2Code);
    openModal();
  };

  return (
    <div>
      <Modal
        isOpen={modalOriginalCodeIsOpen}
        style={customStyles}
        contentLabel="Code Comparison Modal"
        shouldCloseOnOverlayClick={false}
      >
        <div
          style={{ display: "flex", flexDirection: "column", height: "100%" }}
        >
          <div style={{ alignSelf: "flex-end" }}>
            <button onClick={() => modalOriginalCodesetIsOpen(false)}>X</button>
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flex: 1,
              overflow: "auto",
            }}
          >
            <Card
              style={{ width: "100%", overflow: "auto", marginRight: "1%" }}
            >
              <Card.Body>
                <Card.Title>Bad Code</Card.Title>
                <Card.Text style={{ whiteSpace: "pre-wrap" }}>
                  <div
                    dangerouslySetInnerHTML={{ __html: props.data.BadCode }}
                  />
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
          <div style={{ alignSelf: "flex-end" }}>
            <button onClick={() => modalOriginalCodesetIsOpen(false)}>
              Close
            </button>
          </div>
        </div>
      </Modal>

      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        style={customStyles}
        contentLabel="Code Comparison Modal"
        shouldCloseOnOverlayClick={false}
      >
        <div
          style={{ display: "flex", flexDirection: "column", height: "100%" }}
        >
          <div style={{ alignSelf: "flex-end" }}>
            <button onClick={closeModal}>X</button>
          </div>
          <div
            style={{
              display: "flex",
              flexDirection: "row",
              flex: 1,
              overflow: "auto",
            }}
          >
            <Card style={{ width: "32%", overflow: "auto", marginRight: "1%" }}>
              <Card.Body>
                <Card.Title>Bad Code</Card.Title>
                <Card.Text style={{ whiteSpace: "pre-wrap" }}>
                  <div
                    dangerouslySetInnerHTML={{ __html: props.data.BadCode }}
                  />
                </Card.Text>
              </Card.Body>
            </Card>
            <Card style={{ width: "32%", overflow: "auto", marginRight: "1%" }}>
              <Card.Body>
                <Card.Title>Neighbor 1</Card.Title>
                <Card.Text style={{ whiteSpace: "pre-wrap" }}>
                  <div dangerouslySetInnerHTML={{ __html: compareCode1 }} />
                </Card.Text>
              </Card.Body>
            </Card>
            <Card style={{ width: "32%", overflow: "auto" }}>
              <Card.Body>
                <Card.Title>Neighbor 2</Card.Title>
                <Card.Text style={{ whiteSpace: "pre-wrap" }}>
                  <div dangerouslySetInnerHTML={{ __html: compareCode2 }} />
                </Card.Text>
              </Card.Body>
            </Card>
          </div>
          <div style={{ alignSelf: "flex-end" }}>
            <button onClick={closeModal}>Close</button>
          </div>
        </div>
      </Modal>

      <Card border="dark" style={{ margin: "1rem", color: "#000" }}>
        <Card
          onClick={() => modalOriginalCodesetIsOpen(true)}
          border="light"
          style={{
            maxHeight: "30rem",
            overflow: "scroll",
            whiteSpace: "pre-wrap",
          }}
        >
          <Card.Header
            style={{
              fontSize: "0.7em",
              color: "#808080",
              backgroundColor: "#f0f0f0",
              position: "sticky",
              top: 0,
              zIndex: 1,
            }}
          >
            {props.index} Original Code
          </Card.Header>
          <ListGroup horizontal>
            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(props.cve_description[props.data.CVEID])}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.CVEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(props.cwe_description[props.data.CWEID])}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.CWEID}
              </ListGroup.Item>
            </OverlayTrigger>
          </ListGroup>
          <OverlayTrigger
              placement="right-start"
              delay={{ show: 0, hide: 0 }}
              popperConfig={{
                modifiers: [
                  {
                    name: 'offset',
                    options: {
                      offset: [0, -300], // [x-offset, y-offset]
                    },
                  },
                ],
              }}
              overlay={renderTooltip(props.data.CodeSummary)}
            >
          <div dangerouslySetInnerHTML={{ __html: props.data.BadCode }} />
          </OverlayTrigger>
        </Card>

        <Card border="light" onClick={handleCode2vecClick}>
          <Card.Header as="h6" style={{ backgroundColor: "#e6eeff" }}>
            Functional Dimension
          </Card.Header>
          <ListGroup horizontal>
            <ListGroup.Item style={{ color: "#000" }}>
              Neighbor 1 : {props.data.code2vecNeighbor1Rowid}
            </ListGroup.Item>

            <ListGroup.Item style={{ color: "#000" }}>
              similarity : {props.data.code2vecNeighbor1Similarity}
            </ListGroup.Item>
            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cve_description[props.data.code2vecNeighbor1CVEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.code2vecNeighbor1CVEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cwe_description[props.data.code2vecNeighbor1CWEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.code2vecNeighbor1CWEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <ListGroup.Item style={{ color: "#000" }}>
              <div
                dangerouslySetInnerHTML={{
                  __html: props.data.code2vecNeighbor1Signature,
                }}
              />
            </ListGroup.Item>
          </ListGroup>
          <ListGroup horizontal>
            <ListGroup.Item style={{ color: "#000" }}>
              Neighbor 2 : {props.data.code2vecNeighbor2Rowid}
            </ListGroup.Item>
            <ListGroup.Item style={{ color: "#000" }}>
              similarity : {props.data.code2vecNeighbor2Similarity}
            </ListGroup.Item>
            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cve_description[props.data.code2vecNeighbor2CVEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.code2vecNeighbor2CVEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cwe_description[props.data.code2vecNeighbor2CWEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.code2vecNeighbor2CWEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <ListGroup.Item style={{ color: "#000" }}>
              <div
                dangerouslySetInnerHTML={{
                  __html: props.data.code2vecNeighbor2Signature,
                }}
              />
            </ListGroup.Item>
          </ListGroup>
        </Card>

        <Card border="light" onClick={handleSbertClick}>
          <Card.Header as="h6" style={{ backgroundColor: "#ebfaeb" }}>
            Semantic Dimension
          </Card.Header>
          <ListGroup horizontal>
            <ListGroup.Item style={{ color: "#000" }}>
              Neighbor 1 : {props.data.SBERTNeighbor1Rowid}
            </ListGroup.Item>
            <ListGroup.Item style={{ color: "#000" }}>
              similarity : {props.data.SBERTNeighbor1Similarity}
            </ListGroup.Item>
            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cve_description[props.data.SBERTNeighbor1CVEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.SBERTNeighbor1CVEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cwe_description[props.data.SBERTNeighbor1CWEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.SBERTNeighbor1CWEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <ListGroup.Item style={{ color: "#000" }}>
              <div
                dangerouslySetInnerHTML={{
                  __html: props.data.SBERTNeighbor1Signature,
                }}
              />
            </ListGroup.Item>
          </ListGroup>
          <ListGroup horizontal>
            <ListGroup.Item style={{ color: "#000" }}>
              Neighbor 2 : {props.data.SBERTNeighbor2Rowid}
            </ListGroup.Item>
            <ListGroup.Item style={{ color: "#000" }}>
              similarity : {props.data.SBERTNeighbor2Similarity}
            </ListGroup.Item>
            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cve_description[props.data.SBERTNeighbor2CVEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.SBERTNeighbor2CVEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cwe_description[props.data.SBERTNeighbor2CWEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.SBERTNeighbor2CWEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <ListGroup.Item style={{ color: "#000" }}>
              <div
                dangerouslySetInnerHTML={{
                  __html: props.data.SBERTNeighbor2Signature,
                }}
              />
            </ListGroup.Item>
          </ListGroup>
        </Card>

        <Card border="light" onClick={handleCodebertClick}>
          <Card.Header as="h6" style={{ backgroundColor: "#ffe6e6" }}>
            Syntactical Dimension
          </Card.Header>
          <ListGroup horizontal>
            <ListGroup.Item style={{ color: "#000" }}>
              Neighbor 1 : {props.data.CodeBERTNeighbor1Rowid}
            </ListGroup.Item>
            <ListGroup.Item style={{ color: "#000" }}>
              similarity : {props.data.CodeBERTNeighbor1Similarity}
            </ListGroup.Item>
            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cve_description[props.data.CodeBERTNeighbor1CVEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.CodeBERTNeighbor1CVEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cwe_description[props.data.CodeBERTNeighbor1CWEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.CodeBERTNeighbor1CWEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <ListGroup.Item style={{ color: "#000" }}>
              <div
                dangerouslySetInnerHTML={{
                  __html: props.data.CodeBERTNeighbor1Signature,
                }}
              />
            </ListGroup.Item>
          </ListGroup>
          <ListGroup horizontal>
            <ListGroup.Item style={{ color: "#000" }}>
              Neighbor 2 : {props.data.CodeBERTNeighbor2Rowid}
            </ListGroup.Item>
            <ListGroup.Item style={{ color: "#000" }}>
              similarity : {props.data.CodeBERTNeighbor2Similarity}
            </ListGroup.Item>
            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cve_description[props.data.CodeBERTNeighbor2CVEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.CodeBERTNeighbor2CVEID}
              </ListGroup.Item>
            </OverlayTrigger>

            <OverlayTrigger
              placement="right"
              delay={{ show: 0, hide: 0 }}
              overlay={renderTooltip(
                props.cwe_description[props.data.CodeBERTNeighbor2CWEID]
              )}
            >
              <ListGroup.Item style={{ color: "#000" }}>
                {props.data.CodeBERTNeighbor2CWEID}
              </ListGroup.Item>
            </OverlayTrigger>
            <ListGroup.Item style={{ color: "#000" }}>
              <div
                dangerouslySetInnerHTML={{
                  __html: props.data.CodeBERTNeighbor2Signature,
                }}
              />
            </ListGroup.Item>
          </ListGroup>
        </Card>
      </Card>
    </div>
  );
}

const customStyles = {
  content: {
    position: "fixed",
    top: "0",
    left: "0",
    right: "0",
    bottom: "0",
    width: "100%",
    height: "100%",
    color: "#000",
  },
};
export default Item;
