import React from 'react';
import { List } from 'react-virtualized';
import Item from './Item';
import 'bootstrap/dist/css/bootstrap.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {},
      currentPage: 1,
      totalPages: 874, //depends on data set 
      scrollIndex: 0,
      windowSize: {
        width: window.innerWidth,
        height: window.innerHeight,
      },
      isLoading: true,
      isError: false,
    };
  }

  componentDidMount() {
    window.addEventListener("resize", this.handleResize);
    this.fetchData(this.state.currentPage);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleResize);
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.currentPage !== this.state.currentPage ) {
      this.fetchData(this.state.currentPage);
    }
  }

  handleResize = () => {
    this.setState({
      windowSize: {
        width: window.innerWidth,
        height: window.innerHeight,
      },
    });
  };

  fetchData = (page) => {
    this.setState({ isLoading: true, isError: false });
    fetch(`./data/datapart-${page}.json`)
      .then((response) => {
        if (!response.ok) {
          throw new Error("Network response was not ok");
        }
        return response.json();
      })
      .then((data) => this.setState({ data: data, isLoading: false }))
      .catch((error) => {
        console.error('Error:', error);
        this.setState({ isError: true, isLoading: false });
      });
  };

  handleScrollTo = (index) => {
    this.setState({
      scrollIndex: index,
      currentPage: Math.floor(index / 10) + 1,
    });
  };

  getItemData = (index) => {
    let item = this.state.data.listdata[index % 10];
    return item;
  };

  rowRenderer = ({
    key,
    index,
    isScrolling,
    isVisible,
    style,
  }) => {
    const itemData = this.getItemData(index);
    return (
      <div key={key} style={style}>
        <Item data={itemData} index={(this.state.currentPage-1)*10+index} cve_description={this.state.data.cve_description} cwe_description={this.state.data.cwe_description}/>
      </div>
    );
  };

  handleNext = () => {
    if (this.state.currentPage < this.state.totalPages) {
      this.setState(prevState => ({
        currentPage: prevState.currentPage + 1,
      }));
    }
  };

  handlePrev = () => {
    if (this.state.currentPage > 1) {
      this.setState(prevState => ({
        currentPage: prevState.currentPage - 1,
      }));
    }
  };

  render() {
    return (
      <div>
        {this.state.isLoading && <div>Loading...</div>}
        {this.state.isError && <div>An error occurred while fetching data or invalid record.</div>}
        {
          !this.state.isLoading && !this.state.isError && <List
            width={this.state.windowSize.width}
            height={this.state.windowSize.height}
            rowCount={10}
            rowHeight={975}
            rowRenderer={this.rowRenderer}
            scrollToIndex={this.state.scrollIndex % 10}
            scrollToAlignment={"start"}
          />
        }
        <div style={{
          display: 'flex',
          justifyContent: 'space-between',
          padding: '10px',
          position: 'fixed',
          width: '100%',
          bottom: 0,
          backgroundColor: '#fff',
          boxSizing: 'border-box',
        }}>
          <div style={{
            display: 'flex',
            justifyContent: 'center',
            flex: 1,
          }}>
            <button style={{margin: '0 10px'}} onClick={this.handlePrev} disabled={this.state.currentPage === 1}>Prev page</button>
            <input
              type="number"
              placeholder="Go to record"
              style={{margin: '0 10px'}}
              onChange={e => this.handleScrollTo(e.target.value)}
            />
            <button style={{margin: '0 10px'}} onClick={this.handleNext} disabled={this.state.currentPage === this.state.totalPages}>Next page</button>
          </div>
          <div style={{marginLeft: 'auto'}}>Page {this.state.currentPage} of {this.state.totalPages}</div>
        </div>
      </div>
    );
  }
}

export default App;
